.dialogContainer {
  pointer-events: none; }

.dialogContainer,
.dialog {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 300; }

.dialog {
  pointer-events: all;
  color: #303030;
  border-radius: 3px;
  max-width: 350px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1), 0 2px 6px rgba(0, 0, 0, 0.1);
  cursor: default;
  opacity: 0.98;
  line-height: 1.3;
  background: #fff;
  margin: 12px 20px;
  overflow: hidden;
  text-align: left;
  transform: translate3d(0, 0, 0);
  transition: all 0.75s, height 0s;
  transition-timing-function: cubic-bezier(0.25, 1.37, 0.44, 0.93);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
  .dialog.rectangular {
    border-radius: 0px; }
  .dialog.rounded {
    border-radius: 6px; }
  .dialog:hover {
    opacity: 1;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
    transition: 0.35s; }
  .dialog .button.small {
    margin-left: 4px;
    margin-right: 4px; }
  .dialog .button {
    margin-left: 7px;
    margin-right: 7px; }
  .dialogContainer > .dialog {
    position: relative; }
  .dialog.hidden {
    display: none; }
  .dialog.hide {
    transform: scale(0.5);
    opacity: 0 !important;
    transition: height 0s, opacity 0.5s, transform 0.5s !important; }
  .dialog.reveal {
    transition: 0s !important; }
  .dialog .close {
    padding: 12px;
    position: absolute;
    font-size: 0;
    top: 0;
    right: 0;
    cursor: pointer;
    transition: 0.35s;
    opacity: 0;
    z-index: 10; }
    .dialog .close:after, .dialog .close:before {
      content: "";
      width: 14px;
      height: 2px;
      border-radius: 2px;
      background: #202020;
      transform: rotate(45deg);
      position: absolute;
      right: 6px; }
    .dialog .close:before {
      transform: rotate(-45deg); }
    .dialog .close:active {
      opacity: 1; }
    .dialog .close.white {
      background: transparent !important; }
    .dialog .close.white:after, .dialog .close.white:before {
      background: #fff; }
  .dialog:hover .close {
    opacity: 0.2; }
  .dialog .close:hover,
  .mobile .dialog .close {
    opacity: 1 !important; }
  .dialog .dialogContent {
    padding: 20px; }
  .dialog .avatar {
    border-radius: 50px;
    width: 50px;
    height: 50px;
    position: absolute;
    top: 15px; }
  .dialog .avatar + .text {
    width: calc(100% - 70px);
    margin-left: 70px; }
  .dialog .title {
    margin: 8px 0;
    font-weight: 500; }
  .dialog ul {
    display: table;
    width: 100%;
    font-size: 0;
    font-weight: 600;
    box-shadow: 0 1px 0px rgba(0, 0, 0, 0.1) inset; }
  .dialog .hiddenContent {
    overflow: hidden;
    display: block;
    max-height: 0;
    opacity: 0;
    transition: 0.5s; }
  html:not(.page-loaded) .dialog {
    transform: scale(0.5) translateY(100%);
    opacity: 0; }
  .dialog:hover .hiddenContent,
  .dialog.hover .hiddenContent {
    max-height: 90px;
    opacity: 1; }
  .dialog [data-href],
  .dialog [data-dialog-action="close"] {
    cursor: pointer; }
  .dialog li {
    height: 50px;
    font-size: 16px;
    font-weight: 500;
    display: table-cell;
    width: 33.33333%;
    text-align: center;
    cursor: pointer;
    line-height: 1;
    vertical-align: middle;
    text-decoration: none;
    border-bottom: none;
    transition: 0.25s;
    box-shadow: inset 0 -100px 0 rgba(41, 41, 41, 0); }
    .dialog li .material-icons {
      position: relative;
      margin-bottom: 13px;
      top: 6px;
      margin-right: 8px; }
      .dialog li .material-icons.right {
        margin-right: 0;
        margin-left: 8px; }
    .dialog li svg {
      width: 20px;
      height: 20px;
      margin-bottom: -6px;
      margin-right: 8px;
      position: relative;
      top: -2px; }
      .dialog li svg.right {
        margin-right: 0;
        margin-left: 8px; }
  .dialog li {
    border-left: 1px solid rgba(0, 0, 0, 0.1); }
  .dialog li:first-child {
